.App {
  font-family: sans-serif;
  text-align: center;
}

.TableComponent-container {
  text-align: right;
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
}

.TableComponent-items-per-page {
  float: left;
  margin-right: 10px;
  border-radius: 15px;
}

.TableComponent-pagination-container {
  text-align: left;
  margin-left: 5px;
}

.TableComponent-pagination-item {
  padding: 5px;
  cursor: pointer;
  background: lightblue;
  border-radius: 15px;
  margin: 2px;
}

.TableComponent-pagination-active-page {
  background: orange;
}

.TableComponent-pagination-item:hover {
  color: orange;
}

.TableComponent-pagination-bot-container {
  text-align: center;
  margin: 20px auto 20px auto;
}

.TableComponent-show-btn {
  margin: 5px;
}

.TableComponent-search-input {
  margin: 10px 0 10px 10px;
}

.TableComponent-table {
  text-align: center;
  border-collapse: collapse;
  border: 1px solid black;
}

.TableComponent-table-sortable-header {
  cursor: pointer;
}

.TableComponent-table-sortable-header:hover {
  color: coral;
}

th,
td {
  border: 1px solid black;
  padding: 5px;
}

td:first-child {
  width: 30px;
}

td:nth-child(2) {
  min-width: 180px;
}

td:nth-child(3) {
  min-width: 80px;
}

